"use client";
import React, { useEffect, useMemo, useState } from "react";
import { AffiliateAPI } from "@/utils/api";
import { Skeleton } from "@/components/ui/skeleton";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { useToast } from "@/components/ui/toast";
import { Copy, DollarSign, Link2, Users, Wallet } from "lucide-react";

type Dashboard = {
  referrals_count: number;
  amount: number;
  percent: number;
  year: number;
  month: number;
  tenants: string[];
  payouts: { pending_total: number; paid_total: number };
};

type LinkItem = { token: string; url: string; active: boolean };

function formatCurrency(amount: number) {
  return new Intl.NumberFormat("en-US", { style: "currency", currency: "USD" }).format(amount || 0);
}

export default function AffiliateOverviewPage() {
  const { show } = useToast();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [dash, setDash] = useState<Dashboard | null>(null);
  const [links, setLinks] = useState<LinkItem[]>([]);
  const [canCreateMore, setCanCreateMore] = useState(true);
  const [payoutMonth, setPayoutMonth] = useState("");
  const [payoutPercent, setPayoutPercent] = useState<number>(5);
  const [requestingPayout, setRequestingPayout] = useState(false);

  async function refreshAll() {
    setLoading(true);
    try {
      const [d, l] = await Promise.all([
        AffiliateAPI.dashboard(),
        AffiliateAPI.getLinks(),
      ]);
      const dashData = d as Dashboard;
      setDash(dashData);
      if (dashData?.year && dashData?.month) {
        const monthStr = `${dashData.year}-${String(dashData.month).padStart(2, "0")}`;
        setPayoutMonth(monthStr);
      }
      if (dashData?.percent) {
        setPayoutPercent(dashData.percent);
      }
      setLinks((l as any).links || []);
      setCanCreateMore(Boolean((l as any).can_create_more));
      setError(null);
    } catch (e: any) {
      setError(e.message || "Failed to load affiliate data");
      show({ variant: "destructive", title: "Error", description: e.message || "Failed to load" });
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    refreshAll();
  }, []);

  const stats = useMemo(() => ({
    referrals: dash?.referrals_count || 0,
    currentCommission: dash?.amount || 0,
    pendingPayout: dash?.payouts?.pending_total || 0,
    paidPayout: dash?.payouts?.paid_total || 0,
  }), [dash]);

  async function onCreateLink() {
    try {
      await AffiliateAPI.createLink();
      await refreshAll();
      show({ variant: "success", title: "Link created" });
    } catch (e: any) {
      show({ variant: "destructive", title: "Failed to create link", description: e.message });
    }
  }

  async function onDeactivate(token: string) {
    try {
      await AffiliateAPI.deactivate(token);
      await refreshAll();
      show({ variant: "success", title: "Link deactivated" });
    } catch (e: any) {
      show({ variant: "destructive", title: "Failed to deactivate", description: e.message });
    }
  }

  async function onRotate(token: string) {
    try {
      await AffiliateAPI.rotate(token);
      await refreshAll();
      show({ variant: "success", title: "Link rotated" });
    } catch (e: any) {
      show({ variant: "destructive", title: "Failed to rotate", description: e.message });
    }
  }

  async function onCopy(url: string) {
    try {
      if (typeof navigator === "undefined" || !navigator.clipboard) {
        throw new Error("Clipboard not available");
      }
      await navigator.clipboard.writeText(url);
      show({ variant: "success", title: "Link copied", description: "Referral URL saved to clipboard." });
    } catch (e: any) {
      show({ variant: "destructive", title: "Could not copy", description: e?.message || "Clipboard action blocked." });
    }
  }

  async function onRequestPayout() {
    if (!payoutMonth) {
      show({ variant: "destructive", title: "Missing month", description: "Select a month to request payment for." });
      return;
    }
    setRequestingPayout(true);
    try {
      await AffiliateAPI.requestPayout(payoutMonth, payoutPercent || 5);
      show({ variant: "success", title: "Payout requested", description: "We will review and notify you once processed." });
      await refreshAll();
    } catch (e: any) {
      show({ variant: "destructive", title: "Failed to request payout", description: e.message || "Try again later" });
    } finally {
      setRequestingPayout(false);
    }
  }

  if (loading) {
    return (
      <div className="space-y-4">
        <Skeleton className="h-8 w-64" />
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          {Array.from({ length: 4 }).map((_, i) => (<Skeleton key={i} className="h-24" />))}
        </div>
        <Skeleton className="h-8 w-40" />
        <Skeleton className="h-64" />
      </div>
    );
  }

  if (error) {
    return <div className="text-sm text-red-600">{error}</div>;
  }

  const monthLabel = dash?.month ? `${String(dash.month).padStart(2, "0")}/${dash?.year}` : "Current period";
  const cardClass = "rounded-2xl border border-emerald-100 bg-white/80 shadow-sm backdrop-blur-sm";

  return (
    <div className="relative -mx-4 sm:mx-0">
      <div className="absolute inset-0 -z-10 bg-gradient-to-br from-emerald-50 via-white to-emerald-100" />
      <div className="mx-auto max-w-5xl space-y-8 px-4 py-6 sm:px-6 lg:px-8">
        <section className={`${cardClass} flex flex-col gap-6 p-6 sm:flex-row sm:items-center sm:justify-between`}>
          <div className="space-y-3">
            <h1 className="text-2xl font-semibold text-emerald-900">Affiliate Dashboard</h1>
            <p className="text-sm text-emerald-700/80">
              Stay on top of referrals, commissions, and payouts in your affiliate workspace.
            </p>
            <div className="flex flex-wrap gap-3 text-xs text-emerald-700/80">
              <span className="inline-flex items-center gap-2 rounded-full border border-emerald-100 bg-emerald-50 px-3 py-1 font-medium">
                <Users className="h-3.5 w-3.5" /> {(dash?.tenants?.length || 0).toLocaleString()} pharmacies
              </span>
              <span className="inline-flex items-center gap-2 rounded-full border border-emerald-100 bg-emerald-50 px-3 py-1 font-medium">
                <DollarSign className="h-3.5 w-3.5" /> {formatCurrency(stats.currentCommission)} this month
              </span>
            </div>
          </div>
          <Button
            onClick={onCreateLink}
            disabled={!canCreateMore}
            className="h-11 rounded-xl bg-emerald-600 px-5 text-sm font-semibold text-white shadow-md transition hover:bg-emerald-700 disabled:cursor-not-allowed disabled:bg-emerald-200"
          >
            {canCreateMore ? "Create referral link" : "Max 2 links reached"}
          </Button>
        </section>

        <div className="grid gap-4 sm:grid-cols-2 xl:grid-cols-4">
          <CardStat
            title="Referrals"
            value={stats.referrals.toLocaleString()}
            description="Total pharmacies referred"
            icon={<Users className="h-5 w-5" />}
          />
          <CardStat
            title="Current commission"
            value={formatCurrency(stats.currentCommission)}
            description={`Earnings for ${monthLabel.toLowerCase()}`}
            icon={<Wallet className="h-5 w-5" />}
          />
          <CardStat
            title="Pending payouts"
            value={formatCurrency(stats.pendingPayout)}
            description="Awaiting approval"
            icon={<DollarSign className="h-5 w-5" />}
          />
          <CardStat
            title="Paid to date"
            value={formatCurrency(stats.paidPayout)}
            description="All-time processed payouts"
            icon={<Wallet className="h-5 w-5" />}
          />
        </div>

        <div className="grid gap-4 lg:grid-cols-2">
          <section className={`${cardClass} space-y-4 p-6`}>
            <div className="space-y-1">
              <h2 className="text-lg font-semibold text-emerald-900">This month</h2>
              <p className="text-xs text-emerald-700/70">Commission snapshot for {monthLabel.toLowerCase()}.</p>
            </div>
            <div className="grid gap-3 sm:grid-cols-3">
              <MiniStat label="Percent" value={`${dash?.percent ?? payoutPercent}%`} />
              <MiniStat label="Commission" value={formatCurrency(dash?.amount || 0)} />
              <MiniStat label="New pharmacies" value={(dash?.tenants?.length || 0).toString()} />
            </div>
            <div className="rounded-2xl border border-dashed border-emerald-200 bg-emerald-50/70 p-4">
              <p className="text-xs font-medium text-emerald-700/80">Tenant IDs credited this month</p>
              <div className="mt-2 flex max-h-32 flex-wrap gap-2 overflow-auto pr-1">
                {(dash?.tenants || []).length === 0 ? (
                  <span className="rounded-full bg-white px-3 py-1 text-xs text-emerald-600 shadow-sm">No pharmacies yet this month.</span>
                ) : (
                  dash?.tenants?.map((tenant) => (
                    <span key={tenant} className="rounded-full bg-white px-3 py-1 text-xs font-medium text-emerald-700 shadow-sm">
                      {tenant}
                    </span>
                  ))
                )}
              </div>
            </div>
          </section>

          <section className={`${cardClass} space-y-4 p-6`}>
            <div className="space-y-1">
              <h2 className="text-lg font-semibold text-emerald-900">Request payout</h2>
              <p className="text-xs text-emerald-700/70">Submit a commission payout request for a selected month.</p>
            </div>
            <div className="space-y-2">
              <label className="text-xs font-medium text-emerald-600">Month (YYYY-MM)</label>
              <Input
                value={payoutMonth}
                onChange={(e) => setPayoutMonth(e.target.value)}
                placeholder="2025-10"
                className="h-11 rounded-xl border-emerald-200 bg-white/80 focus:border-emerald-400 focus:ring-emerald-400"
              />
            </div>
            <div className="space-y-2">
              <label className="text-xs font-medium text-emerald-600">Commission percent</label>
              <Input
                type="number"
                min={1}
                max={20}
                step={0.5}
                value={payoutPercent}
                onChange={(e) => setPayoutPercent(Number(e.target.value))}
                className="h-11 rounded-xl border-emerald-200 bg-white/80 focus:border-emerald-400 focus:ring-emerald-400"
              />
            </div>
            <Button
              onClick={onRequestPayout}
              disabled={requestingPayout}
              className="w-full h-11 rounded-xl bg-emerald-600 text-sm font-semibold text-white shadow-md transition hover:bg-emerald-700 disabled:cursor-not-allowed disabled:bg-emerald-200"
            >
              {requestingPayout ? "Submitting..." : "Submit payout request"}
            </Button>
          </section>
        </div>

        <section className={`${cardClass} px-0 pb-2`}> 
          <div className="flex flex-col gap-2 border-b border-emerald-100/80 bg-emerald-50/80 px-6 py-4 sm:flex-row sm:items-center sm:justify-between">
            <div className="flex items-center gap-2 text-sm font-semibold text-emerald-900">
              <span className="inline-flex h-8 w-8 items-center justify-center rounded-full bg-emerald-100 text-emerald-600">
                <Link2 className="h-4 w-4" />
              </span>
              Referral links
            </div>
            <div className="text-xs font-medium text-emerald-600">Limit: 2 active links</div>
          </div>
          <div className="divide-y divide-emerald-100/70">
            {links.map((l) => (
              <div
                key={l.token}
                className="flex flex-col gap-4 px-6 py-4 sm:flex-row sm:items-center sm:justify-between"
              >
                <div className="space-y-2">
                  <div className="flex items-center gap-2 text-xs font-medium">
                    <span
                      className={`${
                        l.active
                          ? "bg-emerald-100 text-emerald-700"
                          : "bg-gray-100 text-gray-600"
                      } inline-flex items-center gap-1 rounded-full px-3 py-1`}
                    >
                      <span className="h-2 w-2 rounded-full bg-current" />
                      {l.active ? "Active" : "Inactive"}
                    </span>
                    <span className="font-mono text-[10px] uppercase tracking-widest text-emerald-500/80">
                      {l.token.slice(0, 8)}
                    </span>
                  </div>
                  <div className="flex items-center gap-2 text-sm font-medium text-emerald-900">
                    <Link2 className="h-4 w-4 text-emerald-500" />
                    <span className="break-all font-mono text-xs text-emerald-800/90">{l.url}</span>
                  </div>
                </div>
                <div className="flex flex-wrap gap-2">
                  <Button size="sm" variant="outline" className="rounded-lg border-emerald-200" onClick={() => onCopy(l.url)}>
                    <Copy className="mr-2 h-4 w-4" /> Copy
                  </Button>
                  <Button
                    size="sm"
                    variant="outline"
                    className="rounded-lg border-emerald-200 disabled:border-gray-200 disabled:text-gray-400"
                    onClick={() => onDeactivate(l.token)}
                    disabled={!l.active}
                  >
                    Deactivate
                  </Button>
                  <Button size="sm" variant="outline" className="rounded-lg border-emerald-200" onClick={() => onRotate(l.token)}>
                    Rotate
                  </Button>
                </div>
              </div>
            ))}
            {links.length === 0 && (
              <div className="px-6 py-8 text-center text-sm text-emerald-700/70">
                No referral links yet. Create one to start sharing.
              </div>
            )}
          </div>
        </section>
      </div>
    </div>
  );
}

function CardStat({
  title,
  value,
  description,
  icon,
}: {
  title: string;
  value: React.ReactNode;
  description?: string;
  icon?: React.ReactNode;
}) {
  return (
    <div className="rounded-2xl border border-emerald-100 bg-white/80 p-5 shadow-sm transition hover:shadow-md">
      <div className="flex items-start justify-between gap-3">
        <div>
          <div className="text-xs font-semibold uppercase tracking-wide text-emerald-600">{title}</div>
          <div className="mt-2 text-2xl font-semibold text-emerald-900">{value}</div>
          {description && <p className="mt-2 text-xs text-emerald-700/70">{description}</p>}
        </div>
        {icon && (
          <span className="inline-flex h-10 w-10 items-center justify-center rounded-full bg-emerald-50 text-emerald-600">
            {icon}
          </span>
        )}
      </div>
    </div>
  );
}

function MiniStat({ label, value }: { label: string; value: React.ReactNode }) {
  return (
    <div className="rounded-2xl border border-emerald-100 bg-white/70 p-4 shadow-sm">
      <div className="text-xs font-semibold uppercase tracking-wide text-emerald-600">{label}</div>
      <div className="mt-1 text-lg font-semibold text-emerald-900">{value}</div>
    </div>
  );
}
